       : 80px; \r\n    line-height    : 80px; \r\n\r\n    //search input section.\r\n    input{\r\n      width        : 325px;\r\n      padding      : 5px;\r\n      font-size    : 13px;\r\n      border-radius: 4px;\r\n      outline      : none;\r\n    }\r\n\r\n    //saerch data send button section.\r\n    button{\r\n      width: 50px;\r\n      padding: 5px;\r\n      color: #ffffff;\r\n      background-color: rgba(88,101,242,255);\r\n      border-radius: 4px;\r\n      outline: none;\r\n    }\r\n  }\r\n\r\n  a{\r\n    color          : white;\r\n    font-size      : 13px;\r\n    display        : inline-block;\r\n    vertical-align : top;\r\n    padding        : 0px 15px 0px 15px;\r\n    height         : 80px;        /* if you want it to take the full height of the bar */\r\n    line-height    : 80px;    /* if you want it vertically middle of the navbar */\r\n\r\n    // 2번째 링크부터 hovering 가능하게 설정.\r\n    &:hover:nth-child(n+2){\r\n      opacity      : 0.25;\r\n      transition   : 0.2s ease;\r\n\r\n    }\r\n    // 마지막 로그인 버튼 hovering 시에 애니메이션 작동 및 백그라운드 컬러 적용.\r\n    &:nth-last-child(1){\r\n      width: 64px;\r\n      border-radius: 4px;\r\n      padding-left: 5px;\r\n      padding-right: 5px;\r\n      margin-top: 8px;\r\n      background-color: rgba(88,101,242,255);\r\n      box-shadow       : 0 1px 1px rgba(0,0,0,0.11), \r\n      0 2px 2px rgba(0,0,0,0.11), \r\n      0 4px 4px rgba(0,0,0,0.11), \r\n      0 8px 8px rgba(0,0,0,0.11), \r\n      0 16px 16px rgba(0,0,0,0.11), \r\n      0 32px 32px rgba(0,0,0,0.11);\r\n    }\r\n  }\r\n}\r\n/* 2) logo css */\r\n.logo{\r\n  display          : inline-block;\r\n  vertical-align   : top;\r\n  width            : 64px;\r\n  height           : 64px;\r\n  margin-top       : 8px;    /* if you want it vertically middle of the navbar. */\r\n  border-radius    :25%;\r\n  box-shadow       : 0 1px 1px rgba(0,0,0,0.11), \r\n                    0 2px 2px rgba(0,0,0,0.11), \r\n                    0 4px 4px rgba(0,0,0,0.11), \r\n                    0 8px 8px rgba(0,0,0,0.11), \r\n                    0 16px 16px rgba(0,0,0,0.11), \r\n                    0 32px 32px rgba(0,0,0,0.11);\r\n  /* default : filtering */\r\n  filter           : none;\r\n  /* default : opacity */\r\n  opacity          : 1;\r\n  /* default : transition */\r\n  // transition       : 0.2s ease;\r\n\r\n  /* 멀린 로고 hover css */\r\n  &:hover{\r\n    /* filtering */\r\n    -webkit-filter    : contrast(100%);\r\n    filter            : contrast(100%);\r\n    /* opacity */\r\n    opacity           : 0.25;\r\n    /* transform */\r\n    -webkit-transform : scale(1.05);\r\n    -ms-transform     : scale(1.05);\r\n    transform         : scale(1.05);\r\n    /* rotation */\r\n    -webkit-transform : rotateZ(-5deg);\r\n    -ms-transform     : rotateZ(-5deg);\r\n    transform         : rotateZ(-5deg);\r\n    transition        : all 0.2s ease;\r\n  }\r\n}\r\n\r\n.footer{\r\n  background-color: rgba(21, 21, 21, 1);\r\n  width: 100%;\r\n  height: 80px;\r\n  bottom: 0;\r\n  z-index: 1;\r\n  position: fixed;\r\n}\r\n\r\n@media (max-width: 1000px) {\r\n  .navigation-bar{\r\n    a{\r\n      &:nth-child(n+2){\r\n        display: none;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n@media (max-width: 1000px) {\r\n  .footer{width: 100%}\r\n}\r\n\r\n.login0section{\r\n  border-radius   : 35px;\r\n  padding         : 8px;\r\n  margin          : 0px 100px 0px 100px;\r\n  font-weight     : bolder;\r\n  height          : 100px;\r\n  font-variant    : all-small-caps;\r\n  display         : flex;\r\n  flex-direction  : column;\r\n  align-items     : center;\r\n  justify-content: center;\r\n  box-shadow      : 0 1px 1px rgb(0 0 0 / 11%),\r\n                    0 2px 2px rgb(0 0 0 / 11%),\r\n                    0 4px 4px rgb(0 0 0 / 11%),\r\n                    0 8px 8px rgb(0 0 0 / 11%),\r\n                    0 16px 16px rgb(0 0 0 / 11%),\r\n                    0 32px 32px rgb(0 0 0 / 11%);\r\n}"],"sourceRoot":""}]);
